const Action = {
  //添加参数
  musicCreate (params) {
    let PostContent = params.PostContent;
    if(!PostContent.songs_name) return Promise.reject({Msg: '请输入参数名称', type: 'local'});
    if(!PostContent.singer) return Promise.reject({Msg: '请输入参数名称', type: 'local'});
    if(!PostContent.MusicCode) return Promise.reject({Msg: '请输入参数权限码', type: 'local'});
    return axios.post(REQUEST_URL.musicCreate, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },

  //更新参数
  MusicUpdate (params) {
    let PostContent = params.PostContent;
    if(!PostContent.songs_name) return Promise.reject({Msg: '请输入参数名称', type: 'local'});
    if(!PostContent.singer) return Promise.reject({Msg: '请输入参数名称', type: 'local'});
    if(!PostContent.MusicCode) return Promise.reject({Msg: '请输入参数权限码', type: 'local'});
    return axios.post(REQUEST_URL.MusicUpdate, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  //获取单个参数
  paramsDetailGetSingle (params) {
    return axios.post(REQUEST_URL.paramsDetailGetSingle, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  //删除参数
  musicDelete (params) {
    return axios.post(REQUEST_URL.musicDelete, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  //获取参数列表
  musicGetList (params) {
    return axios.post(REQUEST_URL.musicGetList, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  
  SeedInit(params) {
    let PostContent = params.PostContent;
    if(!PostContent.songs_name) return Promise.reject({Msg: '请输入参数名称', type: 'local'});
    if(!PostContent.singer) return Promise.reject({Msg: '请输入参数名称', type: 'local'});
    return axios.post(REQUEST_URL.SeedInit, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  SeedCreate(params) {
    let PostContent = params.PostContent;
    if(!PostContent.songs_name) return Promise.reject({Msg: '请输入参数名称', type: 'local'});
    if(!PostContent.singer) return Promise.reject({Msg: '请输入参数名称', type: 'local'});
    if(!PostContent.MusicCode) return Promise.reject({Msg: '请输入参数权限码', type: 'local'});
    return axios.post(REQUEST_URL.SeedCreate, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  //获取参数列表
  SeedGetList (params) {
    return axios.post(REQUEST_URL.SeedGetList, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
}

export default Action;



