<template>
    <sub-navigation-frame :title="$route.query.paramsName" :breadcrumb="breadcrumbs">
      <div  slot="navigation" :style="{textAlign:'right',marginTop:'-2.5rem'}">
        <Button @click="$router.push({name: 'Index'})">返回主站</Button>
        <Button @click="$router.go(-1)">返回</Button>
      </div>
      <div class="sub-page-container" :style="{'margin-top': '.5rem','padding-top':'1rem'}" slot="content">
        <Row>
          <Col span="8">
            <div :style="{marginBottom: '.7rem'}">
              <Button type="primary" @click="paramsModal = true" ><Icon type="md-add"></Icon>添加音乐</Button>
            </div>
          </Col>
        </Row>
        <Table :columns="columns" :data="tableData"></Table>
        <Modal
          v-model="paramsModal"
          :title="'编辑' + $route.query.paramsName"
          width=600
          @on-visible-change="$refs.createForm.resetFields()">
          <div>
            <Form
              ref="createForm"
              :model="createModal"
              :rules="createDetailRules">
              <Row>
                <Col span="24">
                  <FormItem class="item-box" label="歌曲名称：" prop="songs_name">
                    <Input v-model="createModal.songs_name" placeholder="请输入歌名" />
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem class="item-box" label="歌手：" prop="singer">
                    <Input v-model="createModal.singer" placeholder="请输入歌手" />
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem class="item-box" label="歌曲：" >
                    <Upload :action="uploadUrl"
                            ref="uploadmusic"
                            :format="['mp3']"
                            :max-size="100000"
                            :before-upload="file => handleBeforeUploadNum($refs['uploadmusic'],file)"
                            :on-success="handleSuccess"
                            :on-format-error="handleFormatError"
                            :on-error="handleError"
                            :on-exceeded-size="handleMaxSize"
                            :show-upload-list="true"
                            :style="{display: 'inline-block'}"
                            paste >
                      <Button @click="flag=1" icon="ios-cloud-upload-outline">上传歌曲</Button>
                    </Upload>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem class="item-box" label="配图：" prop="ContractTemplateName">
                    <Upload :action="uploadUrl1"
                            ref="uploadimage"
                            :format="['jpg','png','jpeg','mp3']"
                            :max-size="100000"
                            :before-upload="file => handleBeforeUploadNum($refs['uploadimage'],file)"
                            :on-success="handleSuccess"
                            :on-format-error="handleFormatError"
                            :on-error="handleError"
                            :on-exceeded-size="handleMaxSize"
                            :show-upload-list="true"
                            :style="{display: 'inline-block'}"
                            paste >
                      <Button icon="ios-cloud-upload-outline" @click="flag=0"  >上传配图</Button>
                    </Upload>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem class="item-box" label="权限码：" prop="MusicCode">
                    <Input v-model="createModal.MusicCode" placeholder="请输入权限码" />
                  </FormItem>
                </Col>
              </Row>
            </Form>
          </div>
          <div slot="footer">
            <Button type="primary" @click="createModal.edit ? updateDetailHandler() : addDetailHandler()">确定</Button>
            <Button @click="paramsModal=false">取消</Button>
          </div>
        </Modal>
      </div>

    </sub-navigation-frame>
</template>

<script>
  import SubNavigationFrame from '../../components/SubNavigationFrame/SubNavigationFrame';
  import Action from './action/addMusic';
  const dataFactory = (params) => {
    return params ? params : {
      MusicCode: '',
      songs_name:'',
      singer:'',
      songs_id:'',
      MusicParentParamID: '',
      MusicParentParamCode:'',
      MusicParentParam: '',
      song_file_url:'',
      song_image_url:'',
      uploadStatus: null,
      uploadList: [],
      ShowOnIndex: false,
      edit: false
    }
  }
    export default {
        name: "addMusic",
      components:{
        SubNavigationFrame,
      },
      data(){
          return{
            breadcrumbs:[
              {title: this.$route.query.paramsName,}
            ],
            paramsModal:false,
            tableData:[],
            createModal:dataFactory(),
            columns:[
              {title: '序号', type: 'index', width: 120},
              {title:'歌名',key:'songs_name'},
              {title:'歌手',key:'singer'},
              {title:'操作', align: 'center', render:this.toolColumnRender}
            ],
            createDetailRules: {
              songs_name: [{required: true, message: '请输入歌名'}],
              singer: [{required: true, message: '请输入歌手'}],
              MusicCode: [{required: true, message: '请输入权限码'}]
            },
            filter: {
              // detailParentParamID: this.$route.query.id,
              // detailParentParamCode: this.$route.query.Code
              songs_name:this.songs_name,
              singer:this.singer,
            },
            uploadUrl:REQUEST_URL.MusicFiles,
            uploadUrl1:REQUEST_URL.MusicImage,
            flag:0,
          }
      },
      mounted () {
        this.$store.commit('showAdminMenu', true);
        this.paramsShowHandler();
      },
      methods: {
        //重置表单
        resetFormHandler(){
          this.createModal = dataFactory();
          this.popModal = true;
        },
        //图片上传前的钩子
        handleBeforeUploadNum(com,file){
          if(!com) return (this.handleError({Msg: '无法获取上传组件', State: -1}, file, this.createModal.uploadList), false);
          if(com.$props.maxSize && (file.size / 1024 > com.$props.maxSize)) return (this.handleError({Msg: '文件体积过大，无法上传', State: -1}, file, this.createModal.uploadList), false);

          this.createModal.uploadStatus = this.$Message.loading({content:'正在上传文档，请稍等...', duration:0});
          const checkNum = this.createModal.uploadList.length;
          if(checkNum) this.$refs.uploadFileRef.clearFiles();
        },
        //上传成功钩子
        handleSuccess(res,file,fileList) {
          console.log(file);
          if(res.State != 1){

            this.handleError(res, file, fileList);
            return fileList.splice(fileList.length - 1 , 1);
          }

          this.createModal.uploadStatus();
          this.createModal.uploadStatus = null;

          if(res.State != 1){
            this.handleError(res, file, fileList);
            return fileList.splice(fileList.length - 1 , 1);
          }
          let form = this.createModal;
          if (this.flag==1){
            console.log('mp3');
            // form.songs_name =file.response.PostContent.name;
            form.song_file_url=file.response.PostContent.Address;
          }else {
            console.log('fdf');
            // form.songs_name =file.response.PostContent.name;
            form.song_image_url=file.response.PostContent.Address;
          }

        },
        //上传文档错误钩子
        handleFormatError(file,fileList) {
          this.createModal.uploadStatus();
          this.createModal.uploadStatus = null;
          this.$Message.warning('文件格式错误，请重新上传');
        },
        //上传失败钩子
        handleError(error,file,fileList) {
          this.createModal.uploadStatus();
          this.createModal.uploadStatus = null;
          this.$Message.warning(error && error.Msg || '文件上传失败');
        },
        //上传文档大于15M钩子
        handleMaxSize(file) {
          this.createModal.uploadStatus();
          this.createModal.uploadStatus = null;
          this.$Message.warning('超出文件大小限制，文档不能超过 15M');
        },
        //操作列渲染
        toolColumnRender(h, params) {
          return h('div', [
            h('Button',{
              props:{type:'text'},
              domProps:{innerText: '编辑'},
              on:{click: e => {
                  this.createModal = dataFactory({
                    songs_name: params.row.songs_name,
                    singer:params.row.singer,
                    MusicCode: params.row.MusicCode,
                    MusicParentParam: params.row.detailParentParam,
                    MusicParentParamID: this.$route.query.id,
                    MusicParentParamCode: this.$route.query.Code,
                    edit: true,
                    songs_id: params.row.songs_id
                  })
                  this.paramsModal = true;
                  e.stopPropagation();
                  e.stopPropagation();
                }
              }
            }),
            h('Button',{
              props:{type:'text'},
              domProps:{innerText: '删除'},
              on:{click: e => {
                  this.deleteDetailHandler(params.row.songs_id)
                  e.stopPropagation();
                }
              }
            })
          ]);
        },
        //根据参数权限码显示页面
        paramsShowHandler(){
          let _filter = objectCopy(this.filter);
          for (let i in _filter) {
            if (!_filter[i]) delete _filter[i]
          }
          Action.SeedGetList({
            PostContent: {
              filter: _filter
            }
          })
            .then(res => {
              this.tableData = res;
              console.log(this.tableData);
            })
            .catch(err => {
              this.$Message.error(err);
            })
        },
        //添加参数详情
        addDetailHandler(){
          // this.createDetailForm.detailParentParamID = this.$route.query.id;
          // this.createDetailForm.detailParentParamCode = this.$route.query.Code;
          // this.createDetailForm.detailParentParam = this.$route.query.paramsName;

          Action.SeedInit({
            PostContent: this.createModal
          })
            .then(res => {
              console.log(this.createModal);
              this.paramsModal = false;
              this.paramsShowHandler();
            })
            .catch(err => {
              console.log("ww111");
              this.$Message.error(err);
            })
        },
        //更新参数详情
        updateDetailHandler(){
          Action.MusicUpdate({
            PostContent: this.createModal
          })
            .then(res => {
              this.paramsModal = false;
              this.paramsShowHandler();
            })
            .catch(err => {
              this.$Message.error(err);
            })
        },
        //删除参数详情
        deleteDetailHandler(ID){
          if (!ID) return this.$Message.warning('参数ID为空，无法删除！！！');
          const modal = this.$Modal.confirm({
            title: '操作确认'
            ,icon: 'warning'
            ,content: '是否删除此参数'
            ,okText: '确定'
            ,showCancel: true
            ,loading: true
            ,onOk: () => {
              //发起删除参数请求
              Action.musicDelete({
                PostContent: {
                  songs_id: ID
                }
              }).then(result => {
                this.$Message.success('成功删除参数！！！');
                this.$Modal.remove()
                this.paramsShowHandler();
              })
                .catch(err => {
                  this.$Modal.remove()
                  this.$Message.error(err);
                });
            }
          });
        }
      }
    }
</script>

<style scoped>

</style>



